.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-container-check {
    position: absolute;
    left: 12px;
}

.Icon-container-check--active {
    color: rgba(15, 231, 15, 0.534);;
}

.Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
}

.Icon-container-delete:hover {
    color: rgba(255, 0, 0, 0.534);;
}

.Icon-svg {
    width: 24px;
    height: 24px;    
}

.Icon-container-delete:hover .Icon-svg{
    fill: rgba(255, 0, 0, 0.534);
}
.Icon-container-check:hover .Icon-svg{
    fill: rgba(15, 231, 15, 0.534);
}